import React from 'react';

import { Container, makeStyles } from '@material-ui/core';
import { renderRoutes } from 'react-router-config';
import { useFavouriteDetector } from 'src/lib/hooks';
import RouteViewer from 'src/views/components/kural/RouteViewer';

const useStyles = makeStyles((theme) => ({
  root: {},
  routes: {
    padding: theme.spacing(1),
  },
}));

const ListPage: React.FC<any> = (props: any) => {
  const classes = useStyles();
  const chapterFavNode = useFavouriteDetector(
    'chapter',
    '/list/:chapter/kural'
  );
  const kuralFavNode = useFavouriteDetector('kural', '/list/kural/:kural');
  return (
    <>
      <Container disableGutters className={classes.routes}>
        <RouteViewer renderRight={chapterFavNode || kuralFavNode} />
      </Container>
      <div className={classes.root}>{renderRoutes(props.route.routes)}</div>
    </>
  );
};

export default ListPage;
