import { firestore } from 'firebase';
import log from 'loglevel';

export const firestoreGetCacheFirst = async (
  ref:
    | firestore.CollectionReference<firestore.DocumentData>
    | firestore.Query<firestore.DocumentData>
): Promise<firestore.QuerySnapshot<firestore.DocumentData>> => {
  let result: firestore.QuerySnapshot<firestore.DocumentData>;
  try {
    result = await ref.get({ source: 'cache' });
    if (result.empty) {
      throw new Error('result not exists in cache');
    }
    log.debug(`fetches from cache`);
  } catch (err) {
    result = await ref.get({ source: 'server' });
  }
  return result;
};
