import React from 'react';

import { makeStyles, Chip } from '@material-ui/core';
import clsx from 'clsx';
import {
  useRoute,
  useNavigation,
  useLongPress,
  RouteProp,
} from 'src/lib/hooks';

const useStyles = makeStyles((theme) => ({
  routeContainer: {
    display: 'flex',
  },
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
  },
  chipRoot: {
    padding: '0px 0.6em',
    fontSize: '0.7rem',
    height: '1.3rem',
    margin: `0 ${theme.spacing(1 / 2)}px ${theme.spacing(1 / 2)}px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type RouteViewerProps = {
  className?: string;
  renderRight?: React.ReactNode;
};

type RouteChipProps = {
  route: RouteProp;
};

const RouteChip: React.FC<RouteChipProps> = ({ route }: RouteChipProps) => {
  const classes = useStyles();
  const [navigateTo] = useNavigation();
  const onLongPress = useLongPress(() => {
    // TODO: favourite
    console.log('long press', route);
  });
  return (
    <Chip
      {...onLongPress}
      size='small'
      label={route.value}
      color={'primary'}
      onClick={(): void => {
        route.route && navigateTo(route.route)();
      }}
      style={{ backgroundColor: route.color }}
      classes={{ root: classes.chipRoot }}
    />
  );
};

const RouteViewer: React.FC<RouteViewerProps> = (props: RouteViewerProps) => {
  const classes = useStyles();
  const [, routes] = useRoute({});
  return (
    <div className={clsx(classes.routeContainer, props.className)}>
      <div className={classes.root}>
        {routes.map((item, index) => (
          <RouteChip route={item} key={index} />
        ))}
      </div>
      {props.renderRight}
    </div>
  );
};

export default RouteViewer;
