import React from 'react';

import { IconButton } from '@material-ui/core';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';

type FavouriteToggleProps = {
  className?: string;
  active: boolean;
  handler: (isActive: boolean) => Promise<boolean>;
};

const FavouriteToggle: React.FC<FavouriteToggleProps> = (
  props: FavouriteToggleProps
) => {
  const [isActive, toggleActive] = React.useState<boolean>(props.active);
  const toggle = (): void => {
    props.handler(!isActive).then((result) => {
      result && toggleActive(!isActive);
    });
  };
  return (
    <IconButton
      color='primary'
      size='small'
      onClick={toggle}
      className={props.className}>
      {isActive && <FavoriteIcon className='active' />}
      {!isActive && <FavoriteBorderIcon className='inactive' />}
    </IconButton>
  );
};

export default FavouriteToggle;
